.statistics {
  margin-top: 80px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 50px;
  width: 500px;
  font-family: sans-serif;
  font-size: 18px;

  border-radius: 8px;
  background: rgb(255, 255, 255);
}
.title {
  margin-bottom: 50px;
  color: grey;
  text-align: center;
  text-transform: uppercase;
}

.list {
display: flex;
width:100%;
}

.item {display: flex;
flex-direction: column;
gap: 10px;
margin-bottom: 10px;
padding-top: 10px;
width: 25%;
align-items: center;
}

.label {
    padding-top: 5px;
    padding-bottom: 5px;
}
.percentage {
    font-size: 22px;
}